import Vue from 'vue'; //  echarts  echarts 

import * as echarts from 'echarts/core'; //  Chart

import { LineChart, GaugeChart } from 'echarts/charts'; //  Component

import { GridComponent, DataZoomComponent } from 'echarts/components'; //  Canvas  CanvasRenderer  SVGRenderer 

import { CanvasRenderer } from 'echarts/renderers'; // 

echarts.use([LineChart, GaugeChart, GridComponent, DataZoomComponent, CanvasRenderer]);
Vue.prototype.$echarts = echarts;